/**
 * Responsive Grid
 * https://share.goabstract.com/4d49facf-fab4-441d-bc05-a24fc0f5281d?mode=design&sha=928afc4be1bbb998036053c3eef3c78775ee6494
 */

@import "spacing";
@import "motion";

// Breakpoints
$particle-rg-xs: 0px;
$particle-rg-sm: 575px;
$particle-rg-md: 839px;
$particle-rg-lg: 1439px;
$particle-rg-xl: 1919px;

// Grid Columns
$particle-rg-columns-xs: 4;
$particle-rg-columns-sm: 8;
$particle-rg-columns-default: 12;
$particle-rg-gutter: $particle-spacing-3x;
$particle-rg-margin: $particle-spacing-6x;

// Particle responsive grid container (fluid)
/**
 * Note, there are no rows in this definition. Hence if you want to for ex. place an element
 * bottom right of your screen, you may need to define your own row and and align it appropriately.
 * Content in general should start from the top of the screen and adapt / wrap wrt to window width.
 */

.particle-rg-container-fluid {
	position: relative;
 	left: $particle-rg-margin;
	display: grid;
	width: calc(100% - #{2*$particle-rg-margin});
	grid-column-gap: $particle-rg-gutter;
	grid-row-gap: $particle-rg-gutter;
	transition: grid-template-columns $particle-duration-medium $particle-easing-standard,
							grid-template-rows $particle-duration-medium $particle-easing-standard;

	@media (min-width: $particle-rg-xs) {
		grid-template-columns: repeat($particle-rg-columns-xs, 1fr);
	}

	@media (min-width: $particle-rg-sm) {
		grid-template-columns: repeat($particle-rg-columns-sm, 1fr);
	}

	@media (min-width: $particle-rg-md) {
		grid-template-columns: repeat($particle-rg-columns-default, 1fr);
	}

	.particle-rg-col-1 { grid-column: auto / span 1; }
	.particle-rg-col-2 { grid-column: auto / span 2; }
	.particle-rg-col-3 { grid-column: auto / span 3; }
	.particle-rg-col-4 { grid-column: auto / span 4; }
	.particle-rg-col-5 { 
		@media (min-width: $particle-rg-sm) {
			grid-column: auto / span 5; 
		}
	}
	.particle-rg-col-6 { 
		@media (min-width: $particle-rg-sm) {
			grid-column: auto / span 6; 
		}
	}
	.particle-rg-col-7 { 
		@media (min-width: $particle-rg-sm) {
			grid-column: auto / span 7; 
		}
	}
	.particle-rg-col-8 { 
		@media (min-width: $particle-rg-sm) {
			grid-column: auto / span 8; 
		}
	}
	.particle-rg-col-9 { 
		@media (min-width: $particle-rg-md) {
			grid-column: auto / span 9; 
		}
	}
	.particle-rg-col-10 { 
		@media (min-width: $particle-rg-md) {
			grid-column: auto / span 10; 
		}
	}
	.particle-rg-col-11 { 
		@media (min-width: $particle-rg-md) {
			grid-column: auto / span 11; 
		}
	}
	.particle-rg-col-12 { 
		@media (min-width: $particle-rg-md) {
			grid-column: auto / span 12; 
		}
	}
}

// Mixins for responsive grid (fluid)
@mixin particle-rg-container-fluid() {
	position: relative;
 	left: $particle-rg-margin;
	display: grid;
	width: calc(100% - #{2*$particle-rg-margin});
	grid-column-gap: $particle-rg-gutter;
	grid-row-gap: $particle-rg-gutter;
	transition: grid-template-columns $particle-duration-medium $particle-easing-standard,
							grid-template-rows $particle-duration-medium $particle-easing-standard;

	@media (min-width: $particle-rg-xs) {
		grid-template-columns: repeat($particle-rg-columns-xs, 1fr);
	}

	@media (min-width: $particle-rg-sm) {
		grid-template-columns: repeat($particle-rg-columns-sm, 1fr);
	}

	@media (min-width: $particle-rg-md) {
		grid-template-columns: repeat($particle-rg-columns-default, 1fr);
	}
}

// Mixin for Levitate responsive grid (fluid)
/*
	@params
  margin (required): px,
	gutter (required): px
*/
$particle-levitate-rg-margin-body: $particle-spacing-3x;
$particle-levitate-rg-gutter-body: $particle-spacing-2x;
$particle-levitate-rg-margin-panel: $particle-spacing-5x;
$particle-levitate-rg-gutter-panel: $particle-spacing-2x;
$particle-levitate-rg-sm: 0px;
$particle-levitate-rg-md: 600px;
$particle-levitate-rg-lg: 904px;

@mixin particle-levitate-rg-container-fluid($margin, $gutter) {
	position: relative;
 	left: $margin;
	display: grid;
	width: calc(100% - #{2*$margin});
	grid-column-gap: $gutter;
	grid-row-gap: $gutter;
	transition: grid-template-columns $particle-duration-medium $particle-easing-standard,
							grid-template-rows $particle-duration-medium $particle-easing-standard;

	@media (min-width: $particle-levitate-rg-sm) {
		grid-template-columns: repeat($particle-rg-columns-xs, 1fr);
	}

	@media (min-width: $particle-levitate-rg-md) {
		grid-template-columns: repeat($particle-rg-columns-sm, 1fr);
	}

	@media (min-width: $particle-levitate-rg-lg) {
		grid-template-columns: repeat($particle-rg-columns-default, 1fr);
	}
}


/*
	@params
  columns (required): integer < 13
*/
@mixin particle-rg-col ($cols) {
	@if $cols < 13 {
		grid-column: auto / span $cols;
	}
}

