@import "colors";
@import "theme-util";

/**
 * typography
 * https://share.goabstract.com/09f892ee-a202-4c42-8e09-55022cc0ac3b?mode=design&sha=bcae922c8f097de2ff0b1b03a6f8e84f4ec75806
 */

// text
$particle-fallback-font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
$particle-fallback-font-family-fixed-width: monospace;

@mixin particle-font-regular() {
    font-family: "Roboto", $particle-fallback-font-family;
    font-weight: normal;
}

@mixin particle-font-medium() {
    font-family: "Roboto", $particle-fallback-font-family;
    font-weight: 500;
}

@mixin particle-font-bold() {
    font-family: "Roboto", $particle-fallback-font-family;
    font-weight: 700;
}

/**
 * Standard mixins
 * IMPORTANT: These are deprecated; please use the ones 
 * the set below prepended with particle-
 */
@mixin html() {
    @include themed(color,--particle-html-color, $particle-deep-space-grey);
}

@mixin body() {
    @include particle-font-regular;
    font-size: 13px;
    line-height: 15px;
    @include themed(color,--particle-body-color, $particle-deep-space-grey);
}

@mixin h1() {
    @include particle-font-medium;
    font-size: 28px;
    line-height: 33px;
    @include themed(color, --particle-h1-color,  $particle-deep-space-grey);
}

@mixin h2() {
    @include particle-font-regular;
    font-size: 24px;
    line-height: 28px;
    @include themed(color, --particle-h2-color, $particle-deep-space-grey);    
}

@mixin h3() {
    @include particle-font-regular;
    font-size: 22px;
    line-height: 25px;
    @include themed(color, --particle-h3-color, $particle-deep-space-grey);        
}

@mixin h4() {
    @include particle-font-medium;
    font-size: 19px;
    line-height: 21px;
    @include themed(color, --particle-h4-color, $particle-deep-space-grey);
}

@mixin a() {
    @include particle-font-regular;
    font-size: 13px;
    line-height: 15px;
    @include themed(color, --particle-a-color, $particle-deep-sky-blue);
}

@mixin a-hover() {
    @include particle-font-regular;
    font-size: 13px;
    line-height: 15px;
    @include themed(color,   --particle-a-hover-color, $particle-deep-sky-blue);
}

