// Overflow
@mixin particle-ellipses-start {
    overflow: hidden;
    text-overflow: ellipsis;
    direction: rtl;
    text-align: left;
    white-space: nowrap; /* Only use this mixin for single line text */
}

@mixin particle-ellipses-end {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap; /* Only use this mixin for single line text */
}

// Responsive Text
/*
  @params
  minVw (required): px
  maxVw (required): px
  minFontSize (required): px
  maxFontSize (required): px

  example:
  @include particle-fluid-text(480px, 1080px, 13px, 64px)
  => inner shadows on right and bottom of element
*/

@function strip-unit($value) {
  @return $value / ($value * 0 + 1);
}

@mixin particle-fluid-text($minVw, $maxVw, $minFontSize, $maxFontSize) {
  font-size: $minFontSize;
  @media screen and (min-width: $minVw) {
    font-size: calc(#{$minFontSize} + #{strip-unit($maxFontSize - $minFontSize)} * ((100vw - #{$minVw}) / #{strip-unit($maxVw - $minVw)}));
  }
  @media screen and (min-width: $maxVw) {
    font-size: $maxFontSize;
  }
}
